
header {
  position: absolute;
  top: 0;
  height: 70px;
  width: 100%;
  z-index: 100;
}
.navbar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100%;
}
.navbar .menu,
.navbar .search {
  width: 25%;
}
.navbar i {
  vertical-align: middle;
}
.menu ul {
  padding: 0;
  margin: 0;
  display: inline;
}
.menu ul li {
  display: inline;
  border-bottom: 1px solid transparent;
  padding: 5px;
}
.menu ul li:hover {
  border-bottom: 1px solid #d50000;
}
.menu ul li i {
  display: none;
}
.menu ul .active a {
  color: #d50000;
}
.logo {
  height: 100%;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.logo img {
  height: 100%;
  padding: 15px;
  display: block;
  margin: 0 auto;
}
.menu {
i
  vertical-align: middle;
}
.search {
  position: relative;
}
.search i {
  position: absolute;
  left: 0px;
  color: #757575;
}
.search input {
  background: transparent;
  border: 0px;
  padding: 0 0 10px;
  border-bottom: 1px solid #212121;
  width: 100%;
  transition: 0.2s ease all;
  text-indent: 35px;
}
.search input:focus {
  outline: none;
  border-bottom: 1px solid #d50000;
}
/* Tablets */
@media all and (max-width: 800px) {
.menu i {
    display: none;
}
.menu ul li span {
    display: none;
}
.menu ul li i {
    display: inline;
}
}
/* Mobile */
@media all and (max-width: 500px) {
.container {
    width: 90%;
}
.menu,
  .search {
    display: none;
}
}


