
body,
html {
  width: 100%;
  height: 100%;
  font-family: 'Roboto', sans-serif;
  margin: 0;
  color: #212121;
}
body body,
html body {
  overflow-y: scroll;
}
* {
  box-sizing: border-box;
}
a:link {
  color: #212121;
  text-decoration: none;
}
a:visited {
  color: #212121;
  text-decoration: none;
}
.button,
button,
input[type="button"] {
  padding: 8px 18px;
  border: 2px solid #de1212;
  color: #fff !important;
  text-decoration: none;
  border-radius: 3px;
  font-size: 12px;
  font-family: 'Montserrat';
  display: inline-block;
}
.container {
  width: 80%;
  margin: 0 auto;
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.5s ease;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}


