
.element {
  width: calc(100% / 3);
}
.element .info {
  color: #757575;
}
.element .info h2 {
  font-family: 'Poppins';
  font-size: 24px;
  color: #212121;
  margin: 15px 0px 5px 0px;
}
.element .info .overview {
  max-height: 100px;
  margin: 15px 0px 50px 0px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 5;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
}
.subinfo div {
  margin: 10px;
  display: inline;
}
.subinfo .stars {
  margin-left: 0;
}
.subinfo .stars i {
  font-size: 10px;
  margin: 0;
}
.subinfo .stars .active {
  color: #de1212;
}
/* Tablets */
@media all and (max-width: 800px) {
.element {
    width: 50%;
}
}
/* Mobile */
@media all and (max-width: 500px) {
.element {
    width: 100%;
}
}


