
.details-wraper {
  position: absolute;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 50;
}
.details-wraper .cover {
  z-index: -10;
  -o-object-fit: cover;
     object-fit: cover;
  height: 100%;
  width: 100%;
  position: fixed;
}
.details-wraper .contents {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 100%;
}
.details-wraper .info {
  color: #f9f9f9;
  background-color: rgba(0,0,0,0.4);
  padding: 20px;
}
.details-wraper .info h1 {
  font-family: 'Poppins';
  margin: -10px 0;
  font-size: 45px;
}
.details-wraper .duration {
  color: #de1212;
}
.details-wraper .subinfo div {
  margin: 10px;
  display: inline;
}
.details-wraper .subinfo .stars {
  margin-left: 0;
}
.details-wraper .subinfo .stars i {
  font-size: 10px;
  margin: 0;
}
.details-wraper .subinfo .stars .active {
  color: #d50000;
}
.details-wraper .subinfo .ten {
  font-size: 24px;
  margin: -5px;
}
.details-wraper .overview {
  margin: 20px 0px;
}


